.todo-container {
    display: grid;
    grid-template-columns: 250px 1fr 250px;

    @include for-mobile-layout {
        grid-template-columns: 
        minmax(20px, 1fr)
        minmax(auto, $mobile-breakpoint)
        minmax(20px, 1fr)
    }

    @include for-narrow-layout {
        grid-template-columns: 
        minmax(20px, 1fr)
        minmax(auto, $narrow-breakpoint)
        minmax(20px, 1fr)
        ;
    }

    @include for-normal-layout {
        grid-template-columns: 
        minmax(20px, 1fr)
        minmax(auto, 1050px)
        minmax(20px, 1fr)
        ;
    }

    > * {
        grid-column: 2;
    }
}

